/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";

/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';

/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";


// font import
@font-face {
  font-family: 'Halis Grotesque';
  font-style: normal;
  font-weight: 400;
  src: local('Halis Grotesque'), url('/assets/fonts/HalisGR-SRegular.otf') format('opentype');
}
body {
  // font-family: 'Halis Grotesque' !important;
  // color: color($colors, black);
}

// header
ion-toolbar{
  	--background: var(--ion-color-base);
}

ion-title{
  --color: var(--ion-color-white);
  font-size: 0.9rem;
}
.bar-button{
  color: var(--ion-color-white);
}

ion-icon{
  font-size: 2rem !important;
  font-weight: bolder;
}
.logo{
  // margin-top: 5px;
  vertical-align: middle;
  //margin-left: 50px;

  position: absolute;
  left: 50%;
  // top: 50%;
  // transform: translate(-50%, -50%);
  transform: translateX(-50%);
  bottom: 2px;
  color: #fafafa;
  font-size: 1rem;
}

.logo-text{
  bottom: 12px !important;
}

  // inputs
  ion-input, ion-textarea, ion-select{
    --color: var(--ion-color-dark) !important;
    border-bottom: 1px solid var(--ion-color-lightgray);
    font-size: 0.7rem !important;
    --background: #f9f9f9 !important;
    --padding-start: 15px !important;
    --placeholder-font-style: normal;
    margin-bottom: 15px;
    border-radius: 5px;

    height: 42px;

    // for select
    --placeholder-color:  var(--ion-color-dark) !important;
    background-color: #f9f9f9 !important;
  }

// buttons
.base-button{
  --background: var(--ion-color-base);
  --color: var(--ion-color-white);
  border: none;
  box-shadow: none;
  height: 42px;
  font-size: 0.9rem;
  font-weight: bold;
  margin: 15px auto;
  text-transform: uppercase;
  --background-activated: var(--ion-color-base);
  --background-focused: var(--ion-color-base);

  // border radius fix
  --border-radius: 5px;
}

.white-button{
  background-color: var(--ion-color-white);
  color: var(--ion-color-black);
  border: 1px solid var(--ion-color-light);
  box-shadow: none;
  height: 42px;
  font-size: 1.8rem;
  font-weight: bold;
  margin: 15px auto;
  text-transform: uppercase;

  &.activated{
    background-color: var(--ion-color-white);
    color: var(--ion-color-black);
  }

  // border radius fix
  border-radius: 5px;
}

.footer-button{
  background-color: transparent;
  color: var(--ion-color-white);
  border: none;
  box-shadow: none;
  // height: 42px;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0;
  height: 2em;

  &.activated{
    background-color: transparent !important;
    color: var(--ion-color-white);
  }

  // border radius fix
  border-radius: 5px;
}

.secondary-button{
  background-color: transparent;
  color: var(--ion-color-black);
  border: 0.6px solid var(--ion-color-base);
  box-shadow: none;
  // height: 42px;
  font-size: 1.2rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 8px 15px;
  height: auto;
  border-radius: 50px;

  &.activated{
    background-color: transparent !important;
    color: var(--ion-color-black);
  }

  // border radius fix
  border-radius: 5px;
}


.chat-icon{
  // height: 35px;
  width: 30px;
  margin: 5px auto;
}

// footer
ion-footer{
  ion-icon{
    font-size: 1.8rem !important;
    display: inline-block;
    margin: 7px auto auto -5px;
  }
}



// login signup forgot common
.login-container, .signup-container, .forgot-container{
  padding: 20px 25px;
}

.login-container{
  .cleaner-btn{
    // margin-top: 75px;
  }
}

.signup-container{
  .cleaner-btn{
    margin-top: 25px;
  }
}

.forgot-container{
  padding-top: 50px;
  color: var(--ion-color-dark);
}


.login-icon{
  margin-right: 20px;
  vertical-align: middle;
}


.action-links{
  text-align: center;
  color: var(--ion-color-gray);
  font-size: 0.8rem;
    ul {
      padding-left: 0;
      margin: auto;
      li{
        list-style: none;
        text-transform: uppercase;
        display: inline-block;
        float: none;
        font-weight: 800;
        padding: 10px 0;

        &::after{
          content: '|';
          margin: auto 6px;
        }

        &:last-child::after{
          content: '';
          margin: auto;
        }
      }
    }

}

.terms{
  padding: 0 10px;
}
.signin{
  color: var(--ion-color-black);
  font-weight: 600;
  span{
    color: var(--ion-color-base) !important;
    text-decoration: underline;
  }
}

// typography
.sub-heading{
  font-size: 0.7rem;
}

.heading{
  font-size: 1rem;
  font-weight: bold;
}

.base-color{
    color: var(--ion-color-base);
}
.bold{
  font-weight: bold;
}

.medium{
  font-weight: 500;
}

.white-color{
  color: var(--ion-color-white);
}

.hr{
  height: 2px;
  border-bottom: 1px solid var(--ion-color-light);
}

// menu
ion-menu{
  ion-toolbar{
    padding: 30px 30px 5px !important;
  }
}


ion-list{
  position: relative;
  margin: auto !important;
  height: 100%;
  ion-item{
    border-bottom: 1px solid var(--ion-color-light) !important;
    ion-label{
        margin: auto !important;
        color: var(--ion-color-dark);
        font-weight: 600;
        text-transform: uppercase;
        font-size: 1.3rem;
        letter-spacing: 1.2px;
    }

    .menu-icon{
      color: var(--ion-color-base);
      vertical-align: middle;
      margin-right: 25px;
      height: 30px;
    }

    &:last-child{
      // border: none !important;
    }
  }


  .button-menu-container{
    padding: 0 15px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .button-menu{
    &.activated{
      background-color: var(--ion-color-base);
    }
  }


  .menu-footer{
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--ion-color-light);
    padding: 5px 15px;
    color: var(--ion-color-gray);
    font-size: 1.2rem;
  }
}


.central-page{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

ion-item:last-child{
  border-bottom: none !important;
}

.notfound{
  text-align: center;
  font-size: 15px;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  margin: auto;

}

.textarea{
  border: 0.9px solid var(--ion-color-light);
}

// action sheet
.moreClass{
  .action-sheet-button{
    font-size: 0.8rem;
    padding: 14px;
    min-height: auto;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--ion-color-dark);
    letter-spacing: 1.2px;
  }
}


.usermenu{
  padding-left: 15px !important;
  margin: auto;
}

.thumb-pic{
  height: 75px;
  width: 75px;
  border-radius: 50%;
  background: var(--ion-color-light);
  position: relative;
  text-transform: uppercase;
  text-align: center;

  .noimg{
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    text-align: center;
    color: var(--ion-color-black);
    font-size: 9px;
    letter-spacing: 0.8px;
  }

  .thumb-icon{
    line-height: 75px;
    font-size: 3rem !important;
    vertical-align: middle;
    margin-top: 10px;
  }
}

.profile-name{
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  color: var(--ion-color-white);
  font-size: 14px;
  letter-spacing: 0.8px;
  font-weight: 600;
  text-transform: uppercase;

  .rating{
    margin: 5px auto;
    font-size: 13px;
    display: block;
    font-weight: normal;
    ion-icon{
      font-size: 14px !important;
    }
  }
}

.central{
    text-align: center;
    margin: auto;
}



.thumb-image{
  height: 100px;
  width: 100px;
  line-height: 145px;
  background: var(--ion-color-light);
  border-radius: 50%;
  position: relative;
  text-align: center;

    .title{
      padding: 10px;
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      transform: translateY(-50%);
      color: var(--ion-color-black);
      font-size: 8px;
      text-transform: uppercase;
      letter-spacing: 0.4px;
      line-height: 10px;
    }

    .thumb-icon{
      line-height: 100px;
      font-size: 4rem !important;
    }
}


/* select modify */

.select-container{
    ion-item{
      margin: 0 !important;

      // border radius fix
      border: 1px solid #e6e6e6 !important;
      border-radius: 5px;
    }
    .select-label{
      color: #9f9f9f;
      font-weight: normal;
    }
}

.remove-cancel .action-sheet-group-cancel{
  display: none !important;
}

// alert designs
.alert {
  padding: 15px;
  // margin-bottom: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  text-align: left;

  &.warning{
    color: #8a6d3b;
    background-color: #fcf8e3;
    border-color: #faebcc;
  }
  &.danger{
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
  }
  &.success{
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }
  &.info{
    color: #31708f;
    background-color: #d9edf7;
    border-color: #bce8f1;
  }
}


// heading title
.heading-title{
  margin: auto auto 20px;
  font-size: 0.8rem;
  font-weight: bolder;
  letter-spacing: 1px;
  color: #9b9b9b;
}


// thumb icon
.thumb-icon{
  font-size: 2rem !important;
  color: darken(#e6e6e6, 10%);
  text-align: center;
}

.notification-icon{
  font-size: 2rem !important;
  color: #fafafa;
}

.tnc{
  color: #007bff;
}


// segment button
.segment-button{
  // border radius fix
  border-radius: 5px !important;
}


// client instructions modal
  .client-instructions-modal{
    //  width: 90%;
    //  height: 80%;
    //  top: 10%;
    //  left: 5%;
    //  right: 5%;
    //  bottom: 10%;

    //  border: 1px solid #ebebeb;
    //  border-radius: 5px;
  }


  // menu buttons
  ion-menu-toggle{
    ion-icon{
      color: #fafafa;
      font-size: 2.2rem !important;
    }
  }

  ion-back-button{
    --color: #fafafa;
    --icon-font-size: 2.2rem;
  }

  ion-item{
    --border-color: transparent;
  }

  // ion segment
  ion-segment{
    --background: transparent;
  }
  ion-segment-button{
    --background: var(--ion-color-white);
    --background-checked: var(--ion-color-base);
    --background-focused: var(--ion-color-base);
    --color: var(--ion-color-base);
    --color-checked: var(--ion-color-white);
    --color-focused: var(--ion-color-white);
    font-size: 0.7rem;

    --indicator-color: transparent !important;
    text-transform: uppercase;
  }

  .booking-buttons-container{
      margin: auto auto 15px;
  }

.popover-content {
    ion-list{
      z-index: 99999;
      ion-item{
        font-size: 0.8rem;
      }
    }
}

ion-toggle{
    --background-checked: var(--ion-color-base)
}

.login-image-container{
  margin: auto;
  margin-bottom: 15px;
  text-align: center;
  .login-image{
    max-height: 100px;
    width: auto;
  }
}

// banner slider
.banner-container{
  margin-bottom: 15px;
}

 .button-white{
    --color: #fafafa !important;
  }

.small-text-button{
  font-size: 0.8rem;
}
